import React, { Component } from 'react';
import Menu, { SubMenu, Item as MenuItem } from '../index';

// const MenuItem = Menu.Item;
class MenuDemo extends Component {
  state = {
    current: 'mail',
    current1: '1'
  }

  handleClick = (e) => {
    console.log('click ', e);
    this.setState({
      current: e.key
    });
  }
  handleClick1 = (e) => {
    console.log('click ', e);
    this.setState({
      current1: e.key
    });
  }
  handleClick2 = (e) => {
    console.log('click ', e);
  }

  render() {
    return (
      <div className="container">
        <h2>`horizontal` mode</h2>
        <Menu onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal">
          <Menu.Item key="mail">
            <i className="glyph-icon glyph-star-full"></i>
          </Menu.Item>
          <Menu.Item key="app">
            <i className="glyph-icon glyph-star-full"></i>
          </Menu.Item>
          <SubMenu title={<span><i className="glyph-icon glyph-star-full"></i> - </span>}>
            <Menu.Item key="setting:1">1</Menu.Item>
            <Menu.Item key="setting:2">2</Menu.Item>
            <Menu.Item key="setting:3">3</Menu.Item>
            <Menu.Item key="setting:4">4</Menu.Item>
          </SubMenu>
          <Menu.Item key="alipay">
            <a href="http://www.alipay.com/" target="_blank"> - </a>
          </Menu.Item>
        </Menu>
        <h2>`inline` Mode</h2>
        <Menu onClick={this.handleClick1} style={{width:240}} defaultOpenKeys={['sub1']} selectedKeys={[this.state.current1]} mode="inline">
          <SubMenu key="sub1" title={<span><i className="glyph-icon glyph-star-full"></i><span></span></span>}>
            <Menu.Item key="1">1</Menu.Item>
            <Menu.Item key="2">2</Menu.Item>
            <Menu.Item key="3">3</Menu.Item>
            <Menu.Item key="4">4</Menu.Item>
          </SubMenu>
          <SubMenu key="sub2" title={<span><i className="glyph-icon glyph-star-full"></i><span></span></span>}>
            <Menu.Item key="5">5</Menu.Item>
            <Menu.Item key="6">6</Menu.Item>
            <SubMenu key="sub3" title="">
              <Menu.Item key="7">7</Menu.Item>
              <Menu.Item key="8">8</Menu.Item>
            </SubMenu>
          </SubMenu>
          <SubMenu key="sub4" title={<span><i className="glyph-icon glyph-star-full"></i><span></span></span>}>
            <Menu.Item key="9">9</Menu.Item>
            <Menu.Item key="10">10</Menu.Item>
            <Menu.Item key="11">11</Menu.Item>
            <Menu.Item key="12">12</Menu.Item>
          </SubMenu>
        </Menu>
        <h2>`vertical` Mode</h2>
        <Menu onClick={this.handleClick2} style={{width:240}} mode="vertical">
          <SubMenu key="sub1" title={<span><i className="glyph-icon glyph-star-full"></i><span></span></span>}>
            <MenuItem key="1">1</MenuItem>
            <MenuItem key="2">2</MenuItem>
            <MenuItem key="3">3</MenuItem>
            <MenuItem key="4">4</MenuItem>
          </SubMenu>
          <SubMenu key="sub2" title={<span><i className="glyph-icon glyph-star-full"></i><span></span></span>}>
            <MenuItem key="5">5</MenuItem>
            <MenuItem key="6">6</MenuItem>
            <SubMenu key="sub3" title="">
              <MenuItem key="7">7</MenuItem>
              <MenuItem key="8">8</MenuItem>
            </SubMenu>
          </SubMenu>
          <SubMenu key="sub4" title={<span><i className="glyph-icon glyph-star-full"></i><span></span></span>}>
            <MenuItem key="9">9</MenuItem>
            <MenuItem key="10">10</MenuItem>
            <MenuItem key="11">11</MenuItem>
            <MenuItem key="12">12</MenuItem>
          </SubMenu>
        </Menu>
      </div>
    );
  }
}

export default MenuDemo;



