import React, { Component } from 'react';
import Tag from '../TagItem';
import ReactTags from '../ReactTags';

class TagDemo extends Component {
  state = {
    tags: [ {id: 1, text: "Apples"} ],
    suggestions: ["BananaBanana","BananaBanana1","BananaBanana2","BananaBanana3", "Mango", "Pear", "Apricot"]
  }

  handleDelete = (i) => {
    let tags = this.state.tags;
    tags.splice(i, 1);
    console.log('current tags: ', tags);
    this.setState({tags: tags});
  }

  handleAddition = (tag) => {
    let tags = this.state.tags;
    tags.push({
        id: tags.length + 1,
        text: tag
    });
    this.setState({tags: tags});
  }

  onDelete = (e) => {
    console.log(e);
  }
  render () {
    let tags = this.state.tags;
    let suggestions = this.state.suggestions;
    return (
      <div>
        <div className="container">
          <h2> closable </h2>
          <Tag></Tag>
          <Tag></Tag>
          <Tag closable onDelete={this.onDelete}></Tag>
          <Tag href="http://www.baidu.com"></Tag>
        </div>
        <div className="container">
          <h2></h2>
          <Tag closable></Tag>
          <Tag closable amStyle="primary"></Tag>
          <Tag closable amStyle="default"></Tag>
          <Tag closable amStyle="secondary"></Tag>
          <Tag closable amStyle="warning"></Tag>
          <Tag closable amStyle="danger"></Tag>
        </div>
        <div className="container">
          <h2>TAG</h2>
          <ReactTags tags={this.state.tags}
            suggestions={this.state.suggestions}
            amStyle={'primary'}
            labelField = {'text'}
            handleDelete={this.handleDelete}
            handleAddition={this.handleAddition} />
        </div>
      </div>
    );
  }
}

export default TagDemo;



